import { FC } from 'react';
import { Link } from 'react-router-dom';
import { BurgerIcon, ListIcon, Logo, ProfileIcon } from '@ya.praktikum/react-developer-burger-ui-components';
import styles from './app-header.module.css';
import { useEffect, useState } from 'react';
import { useLocation } from 'react-router-dom';

const AppHeader: FC = () => {

  const [active, setActive] = useState('');
  const location = useLocation();

  useEffect(() => {
    if (location.pathname.startsWith('/feed')) setActive(' ');
    else if (location.pathname.startsWith('/profile') || location.pathname.startsWith('/login')) setActive(' ');
    else setActive('');
  }, [location]);

  return (
    <header className={styles.header}>
      <div className={styles.container}>
        <ul className={styles.list}>
          <li className={`${styles.item} mr-2`}>
            <Link to='/' className={`${styles.link} pr-5 pl-5`} onClick={() => setActive('')}>
              <BurgerIcon type={`${active === '' ? "primary" : "secondary"}`} />
              <p className={`text text_type_main-default pl-2 ${active === '' ? "" : "text_color_inactive"}`}>
                
              </p>
            </Link>
          </li>
          <li className={`${styles.item} mr-28`}>
            <Link to='/feed' className={`${styles.link} pr-5 pl-5`} onClick={() => setActive(' ')}>
              <ListIcon type={`${active === ' ' ? "primary" : "secondary"}`} />
              <p className={`text text_type_main-default pl-2 ${active === ' ' ? "" : "text_color_inactive"}`}>
                 
              </p>
            </Link>
          </li>
        </ul>
        <Logo />
        <div className={`${styles.item} ml-2`}>
          <Link to='/profile' className={`${styles.link} pr-5 pl-5`} onClick={() => setActive(' ')}>
            <ProfileIcon type={`${active === ' ' ? "primary" : "secondary"}`} />
            <p className={`text text_type_main-default pl-2 ${active === ' ' ? "" : "text_color_inactive"}`}>
               
            </p>
          </Link>
        </div>
      </div>
    </header>)
}

export default AppHeader;
